  // render image list on front-end
  renderImages () {
    let ref = this;
    return (
        <span className="col">
            <div className="row row-img">
                {this.state.images.map(function(image, idx) {
                  if (idx%5 === 0){
                    ref.state.counter +=5
                    return (
                      <div key={idx} className="col-lg-5 col-md-5 col-sm-12 image-container" >
                          <a href={image.src} download >
                            <img src={image.src} className={'curson-type-pointer image ' + image.class}/>
                          </a>
                      </div>
                    );
                  }
                  else if (idx%4 === 0){
                    ref.state.counter +=4
                    return (
                      <div key={idx} className="col-lg-4 col-md-4 col-sm-12 image-container" >
                        <a href={image.src} download >
                            <img src={image.src} className={'curson-type-pointer image ' + image.class}/>
                        </a>
                      </div>
                    );
                  }
                  else if (idx%3 === 0){
                    ref.state.counter +=3
                    return (
                      <div key={idx} className="col-lg-3 col-md-3 col-sm-12 image-container" >
                        <a href={image.src} download >
                            <img src={image.src} className={'curson-type-pointer image ' + image.class}/>
                        </a>
                      </div>
                    );
                  }
                  else {
                    ref.state.counter +=2
                    return (
                      <div key={idx} className="col-lg-2 col-md-2 col-sm-12 image-container" >
                        <a href={image.src} download >
                            <img src={image.src} className={'curson-type-pointer image ' + image.class}/>
                        </a>
                      </div>
                    );
                  }
                })}
            </div> 
        </span>
    )
}
